.svg-tooltip {
    background-color: white;
    border: 1px #a0a0a0 solid;
    border-radius: 2px;
    z-index: 1000;
}

.tooltip-attr {
    color: #5f6bbe;
    pointer-events: none;
    font-family: Consolas, Menlo, monospace;
    font-weight: 400;
    background-color: #f8f8f8;
    border: 1px solid #e0e0e0;
    padding: 0 3px 0 3px;
    border-radius: 5px;
    margin: 0 2px 0 2px;
}

.ant-tree-switcher-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(0, -50%);
}
